<template>
	<div class="blockdL">
		<div class="breadcrumb">
		    <el-breadcrumb separator="/">
		        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
		        <el-breadcrumb-item></el-breadcrumb-item>
		    </el-breadcrumb>
		</div>

		<div class="article">
			<h2></h2>

			<div class="edit">
			<div class="edit">
				<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
					<el-form-item label="" style="display: inline-block;">
						<el-input placeholder=""  v-model="searchItem.likeName" @keyup.enter.native="searchItemMethod"></el-input>
					</el-form-item>
					<el-form-item style="display: inline-block;">
						<el-button type="primary"  @click="searchItemMethod" ></el-button>

					</el-form-item>
				</el-form>
			</div>
			</div>

			<div class="table">
				<el-table border style="width: 100%" height="440" :data="allItems">
					<el-table-column prop="userid" label="ID"></el-table-column>
					<el-table-column prop="blockeduserid" label="ID"></el-table-column>
					<el-table-column prop="blockedtime" label="">
						<template scope="scope">
					        <span >{{ scope.row.blockedtime|discount}}</span>
					    </template>
					</el-table-column>
					<el-table-column prop="blockedstatus" label="">
						<template scope="scope">
					        <span >{{ scope.row.blockedstatus|discounts}}</span>
					    </template>
					</el-table-column>
					<el-table-column label="" width="300">
						<template scope="scope">
							<el-button type="text" @click="openEditDialogMethod(scope.$index)"> &amp; </el-button>
							<!--<el-button type="text"  @click="deleteItemMethod(scope.$index)"></el-button>-->
						</template>
					</el-table-column>
				</el-table>
			</div>
			<!---->
			<div class="block">
			    <el-pagination
			   	  @current-change="blacklist"
			      :current-page="page"
			      layout="prev, pager, next, jumper"
			      :total="total"
			      >
			    </el-pagination>
			</div>
			<el-dialog title=" & " v-model="isEdit">
				<el-form :model="editItem">
					<el-form-item label="blockedlistid" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" v-model="editItem.blockedlistid"></el-input>
					</el-form-item>
					<el-form-item label="userid" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.userid"></el-input>
					</el-form-item>
					<el-form-item label="blockeduserid" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.blockeduserid"></el-input>
					</el-form-item>
					<el-form-item label="blockedstatus" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.blockedstatus"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="editItemMethod"></el-button>
					<el-button @click="closeEditDialogMethod"> </el-button>
				</div>
			</el-dialog>
			</div>
		</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'

	export default {
		data () {
			return {
				//
				page:1,
				//
				total:1,
				//  v-model 
				searchItem: {
					likeName: '',
				},
				
				// 
				allItems: [{
					userid: '',
					blockeduserid: '',
					blockedtime: '',
					blockedstatus: '',
				}],

				formLabelWidth: '120px',
				
				// edit  flag editIndex v-model 
				isEdit: false,
				editIndex: '',
				editItem: {},
				//saveItem: {},
			};
		},
		filters: {
              discounts : function(val){
				if(val==0){
					val=""
				}else{
					val=""
				};
				var t = val
				return t
			              }
			        },
		created () {
			//  allItems
			this.getAllItemsMethod();
		},

		methods: {
			//  allItems
			getAllItemsMethod () {
				axios.post('/eyed/web/friend/findBlockedList.action',qs.stringify({
					current:1,
					length:3
				}))
				.then((response) => {

					this.allItems = response.data.result.resultList;
					this.total = (response.data.result.count/3)*10
					console.log(response.data);
				})
				.catch((error) => {
					console.log(error);
				});
			},
			blacklist(val){
				//
				document.getElementsByClassName("el-table__body-wrapper")[0].scrollTop=0;
				let searchItem = this.searchItem;
				this.page=val;
				axios.post('/eyed/web/friend/findBlockedList.action', qs.stringify({
					current:val,
					length:3,
					likeStr:searchItem.likeName,
				}))
				.then((response) => {
					this.allItems = response.data.result.resultList;
					
				})
				.catch((error) => {
					console.log(error);
				});
			},

			//  & 
			clone (obj) {
				let newObj = {};
				for (let key in obj) {
					let val = obj[key];
					newObj[key] = typeof val === 'object' ? this.clone(val) : val;
				}
				return newObj;
			},
			openEditDialogMethod (index) {
				this.isEdit = true;
				//  item
				this.editIndex = index;
				this.editItem = this.clone(this.allItems[index]);
			},
			closeEditDialogMethod () {
				this.isEdit = false;
				// 
				this.editItem = {
					blockedlistid: '',
					userid: '',
					blockeduserid: '',
					blockedstatus: '',
				};
			},
			editItemMethod () {
				let editItem = this.editItem;
				axios.post('/eyed/web/updateBlockedList.action', qs.stringify({
					userid: editItem.userid,
					blockedlistid:editItem.blockedlistid,
					blockeduserid:editItem.blockeduserid,
					blockedstatus: editItem.blockedstatus,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.closeEditDialogMethod();
						this.getAllItemsMethod();
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			searchItemMethod () {
				let searchItem=this.searchItem;
				this.page=1;
				axios.post('/eyed/web/friend/findBlockedList.action', qs.stringify({
					likeStr:searchItem.likeName,
					current:this.page,
					length:3
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						if(response.data.result.count=='0'){
							alert("")
						}else{
						this.allItems= response.data.result.resultList;
						this.total = (response.data.result.count/3)*10
						}
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},
		},
	};
</script>



