
<template>
	<div class="blockdL">
		<div class="breadcrumb">
		    <el-breadcrumb separator="/">
		        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
		        <el-breadcrumb-item></el-breadcrumb-item>
		    </el-breadcrumb>
		</div>
		<div class="article">
			<h2></h2>
			<div class="edit">
					<div class="edit">
					<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
					<el-form-item label="">
						<el-input placeholder="ID"  v-model="searchItem.reportid" @keyup.enter.native="searchItemMethod"></el-input>
					</el-form-item>

					<el-form-item>
						<el-button type="primary"  @click="searchItemMethod"  class="problemUser"></el-button>
					</el-form-item>
				</el-form>
			</div>
			</div>
			<div class="table">
			<el-table :data="allItems" border style="width: 100%" max-height="550">
    <el-table-column fixed	prop="reportdate"  label=""  width="220">
    </el-table-column>
    <el-table-column prop="reportdate" label=""  width="150">
    </el-table-column>
      <el-table-column prop="reportid" label="ID"  width="150">
    </el-table-column>
    <el-table-column prop="beinformeruserid" label="ID"  width="150">
    </el-table-column>
    <el-table-column prop="reportreason" label=""  width="300">
    </el-table-column>
     <el-table-column prop="status" label=""  width="120">
    </el-table-column>
     <el-table-column prop="disposeId" label="ID"  width="120">
    </el-table-column>
      <el-table-column prop="disposetime" label=""  width="220">
    </el-table-column>
    <el-table-column prop="mark" label=""  width="300">
    </el-table-column>
    <el-table-column   fixed="right" label=""  width="200">
      <template scope="scope">
      	  <el-button  type="text"  size="small">
          &amp; 
        </el-button>
      </template>
    </el-table-column>
  </el-table>


			</div>
				<!---->
			<div class="block">
			    <el-pagination
			   	  @current-change="problemUser"
			      :current-page="page"
			      layout="prev, pager, next, jumper"
			      :total="total"
			      >
			    </el-pagination>
			 </div>


			</div>

		</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	export default {
		data(){
			return {
				//
				page:1,
				//
				total:1,
  
				allItems:[{
					beinformeruserid:'',
					disposeId:'',
					disposetime:'',
					informeruserid:'',
					mark:'',
					reportdate:'',
					reportid:'',
					reportreason:'',
					status:''
				}],
				searchItem: {
					reportid: '',
				},
			}
		},
		created () {
			//  allItems
			this.getAllItemsMethod();
		},

		methods:{
			getAllItemsMethod(){
				axios.post('/eyed/web/problemuser/selectProblemusers.action',qs.stringify({
					current:1,
				}))
				.then((response)=>{
						if (response.data.code == '0000') {
						
						this.allItems = response.data.result.resultList;
						this.total = response.data.result.count/5
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error)=>{
					console.log(error);
				});
			},
			problemUser(val){
				//
				document.getElementsByClassName("el-table__body-wrapper")[0].scrollTop=0;
				let searchItem = this.searchItem;
				this.page=val;
				axios.post('/eyed/web/problemuser/selectProblemusers.action', qs.stringify({
					current:val,
					reportid:searchItem.reportid,
				}))
				.then((response) => {
					this.allItems = response.data.result.resultList;
					
				})
				.catch((error) => {
					console.log(error);
				});
			},
			searchItemMethod () {
				
				let searchItem = this.searchItem;
				axios.post('/eyed/web/problemuser/selectProblemusers.action', qs.stringify({
					current:1,
					reportid:searchItem.reportid,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
					
						this.allItems= response.data.result.resultList;
						this.total = response.data.result.count/5
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});


			},

		}
		}


</script>
<style>
	.problemUser{
		position: absolute;
		top:-58px;
		right: -70px;
	}
</style>






