				unPagel:1,
				//
				total:1,
				unTotal:1,
				unTotall:1,
				//  v-model 
				searchItemY:{
					eyednumber: '',
				},
				searchItem: {
					eyednumber: '',
				},
				// 
				allItemsW: [{
					eyednumber: '',
				}],
				allItemsWl: [{
					eyednumber: '',
				}],
				distribution:{
					userId:'',
					eyednumber: '',
				},
					// 
				allItemsY: [{
					eyednumber: '',
					deleteEyedcodereason:'',
					codeType:'',
					removereason:'',
					updateTime:''
				}],

				formLabelWidth: '120px',
				// add  flag v-model 
				isAdd: false,
				
				isDel:false,
				delItem: {
					eyednumber: '',
