<template>
	<div class="usermanagement">
		<div class="breadcrumb">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>

		<div class="article">
			<h2></h2>

			<div class="edit">
				<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
					<el-form-item label="id" style="display: inline-block;">
						<el-input placeholder="id"  v-model="searchItem.userId" @keyup.enter.native="searchItemMethod"></el-input>
					</el-form-item>
					<el-form-item style="display: inline-block;">
						<el-button type="primary"  @click="searchItemMethod" ></el-button>

					</el-form-item>
				</el-form>
			</div>

			<div class="table">
				<el-table border style="width: 100%" height="440" :data="allItems">
					<el-table-column prop="focusID" label="id" width="100"></el-table-column>
					<el-table-column prop="userID" label="id"></el-table-column>
					<el-table-column prop="focususerID" label="id"></el-table-column>
					<el-table-column prop="focustime" label=""></el-table-column>
					<el-table-column label="" width="200">
						<template scope="scope">
							<el-button type="text" > &amp; </el-button>
							<!--<el-button type="text"  ></el-button>-->
						</template>
					</el-table-column>
				</el-table>
			</div>
			<!---->
			<div class="block">
			    <el-pagination
			   	  @current-change="attentionRelationship"
			      :current-page="page"
			      layout="prev, pager, next, jumper"
			      :total="total"
			      >
			    </el-pagination>
			 </div>



		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'

	export default {
		data () {
			return {
				//
				page:1,
				//
				total:1,
				//  v-model 
				searchItem: {
					likeName: '',
				},

				// 
				allItems: [{
					focusid: '',
					userid: '',
					focususerid: '',
					focustime: '',

				}],

				formLabelWidth: '120px',
				// add  flag v-model 
				isAdd: false,
				addItem: {
					labelid: '',
					//createtime: '',
					labelname: '',
					labelimagepath: '',
					labeltype: '',
				},

				// edit  flag editIndex v-model 
				isEdit: false,
				editIndex: '',
				editItem: {},
				//saveItem: {},
			};
		},

		created () {
			//  allItems
			this.getAllItemsMethod();
		},

		methods: {
			//  allItems
			getAllItemsMethod () {
				axios.post('/eyed/web/friend/selectFocusrelationShipForWeb.action',qs.stringify({
					current:1,
					length:3
				}))
				.then((response) => {
					this.allItems = response.data.result.resultList;
					this.total = (response.data.result.count/3)*10
				})
				.catch((error) => {
					console.log(error);
				});
			},
			attentionRelationship(val){
				//
				document.getElementsByClassName("el-table__body-wrapper")[0].scrollTop=0;
				let searchItem = this.searchItem;
				this.page=val;
				axios.post('/eyed/web/friend/selectFocusrelationShipForWeb.action', qs.stringify({
					current:val,
					length:3,
					likeStr:searchItem.likeName,
				}))
				.then((response) => {
					this.allItems = response.data.result.resultList;
					
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
					searchItemMethod () {
				let searchItem = this.searchItem;
				this.page=1;
				axios.post('/eyed/web/friend/selectFocusrelationShipForWeb.action', qs.stringify({
					userId:searchItem.userId,
					current:1,
					length:3
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						
						this.allItems= response.data.result.resultList;
						this.total = (response.data.result.count/3)*10				
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {
					alert("")
					console.log(error);
				});

			},
		},
	};
</script>



