<template>
	<div class="usermanagement">
		<div class="breadcrumb">
		    <el-breadcrumb separator="/">
		        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
		        <el-breadcrumb-item></el-breadcrumb-item>
		    </el-breadcrumb>
		</div>

		<div class="article">
			<h2></h2>

			<div class="edit">
				<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
					<el-form-item label="ID">
						<el-input placeholder="ID"  v-model="searchItem.likeName" @keyup.enter.native="searchItemMethod"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="searchItemMethod"></el-button>
						<el-button type="info" :plain="true" @click="openAddDialogMethod"></el-button>
					</el-form-item>
				</el-form>
			</div>
			<!---->
			<div class="table">
				<el-table border style="width: 100%" height="440" :data="allItems">
					<el-table-column prop="nknId" label="ID"></el-table-column>
					<el-table-column prop="nNick" label=""></el-table-column>
					<el-table-column label="" width="300">
						<template scope="scope">
							<el-button type="text" @click="openEditDialogMethod(scope.$index)"> &amp; </el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
			<!---->
			  <div class="block">
			    <el-pagination
			      @current-change="nounPaging"
			      :current-page="page"
			      layout="prev, pager, next, jumper"
			      :total="total"
			      >
			    </el-pagination>
			  </div>
				
			<!---->
			<el-dialog title="" v-model="isAdd">
				<el-form :model="addItem">
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" v-model="addItem.nknId"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="addItem.nNick"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="addItemMethod"> </el-button>
					<el-button @click="closeAddDialogMethod"> </el-button>
				</div>
			</el-dialog>
			<!---->
			<el-dialog title=" & " v-model="isEdit">
				<el-form :model="editItem">
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" v-model="editItem.nknId"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.nNick"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="editItemMethod"></el-button>
					<el-button @click="closeEditDialogMethod"> </el-button>
				</div>
			</el-dialog>

		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'

	export default {
		data () {
			return {
				//
				page:1,
				//
				total:1,
				//  v-model 
				searchItem: {
					likeName:'',
				},

				// 
				allItems: [{
					nknId: '',
					nNick: '',
				}],

				formLabelWidth: '120px',
				// add  flag v-model 
				isAdd: false,
				addItem: {
					nknId: '',
					nNick: '',
				},

				// edit  flag editIndex v-model 
				isEdit: false,
				editIndex: '',
				editItem: {},
				//saveItem: {},
			};
		},

		created () {
			//  allItems
			this.getAllItemsMethod();
		},

		methods: {
			//  allItems
			getAllItemsMethod () {
				axios.post('/eyed/web/nickManager/seletedAllNickN.action', qs.stringify({
					current:1,
				}))
				.then((response) => {
					this.allItems = response.data.result.resultList;
					this.total = response.data.result.count/5
					console.log(this.allItems)
				})
				.catch((error) => {
					console.log(error);
				});
			},
			//
			nounPaging(val){
				let searchItem = this.searchItem;
				//
				document.getElementsByClassName("el-table__body-wrapper")[0].scrollTop=0;
				//
				this.page=val;
				axios.post('/eyed/web/nickManager/seletcdDNickNLikeNick.action', qs.stringify({
					current:val,
					likeStr:searchItem.likeName,
				}))
				.then((response) => {
					this.allItems = response.data.result.resultList;
					
				})
				.catch((error) => {
					console.log(error);
				});
			},


			// 
			openAddDialogMethod () {
				this.isAdd = true;
			},
			closeAddDialogMethod () {
				this.isAdd = false;
				// 
				this.addItem = {
					nknId: '',
					nNick: '',
				};
			},
			addItemMethod () {
				let addItem = this.addItem;
				axios.post('/eyed/web/nickManager/addNickN.action', qs.stringify({
					NickN: addItem.nNick,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						//  allItems
						this.getAllItemsMethod();
						//  dialog
						this.closeAddDialogMethod();
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
//			deleteItemMethod (index) {
//				let deleteItem = this.allItems[index];
//				axios.post(this.api + '/eyed/web/deleteNickN.action', qs.stringify({
//					NickNID: deleteItem.nknId,
//				}))
//				.then((response) => {
//					if (response.data.code == '0000') {
//						//  index 
//						this.allItems.splice(index, 1);
//					} else {
//						console.log('0000');
//					};
//				})
//				.catch((error) => {
//					console.log(error);
//				});
//			},
//
			//  & 
			clone (obj) {
				let newObj = {};
				for (let key in obj) {
					let val = obj[key];
					newObj[key] = typeof val === 'object' ? this.clone(val) : val;
				}
				return newObj;
			},
			openEditDialogMethod (index) {
				this.isEdit = true;
				//  item
				this.editIndex = index;
				this.editItem = this.clone(this.allItems[index]);
			},
			closeEditDialogMethod () {
				this.isEdit = false;
				// 
				this.editItem = {
					nknId: '',
					nNick: '',
				};
			},
			editItemMethod () {
				let editItem = this.editItem;
				axios.post( '/eyed/web/nickManager/updateNickN.action', qs.stringify({
					NickNID: editItem.nknId,
					NickNNick: editItem.nNick,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.closeEditDialogMethod();
						this.getAllItemsMethod();
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			searchItemMethod () {
				document.getElementsByClassName("el-table__body-wrapper")[0].scrollTop=0
				let searchItem = this.searchItem;
				//
				this.page=1;
				axios.post('/eyed/web/nickManager/seletcdDNickNLikeNick.action', qs.stringify({
					likeStr:searchItem.likeName,
					current:1
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						if(response.data.result.count=='0'){
							alert("")
						}else{
						this.allItems = response.data.result.resultList
						this.total = response.data.result.count/5
						}
					} else {
						alert("");
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},
		},
	};
</script>



