<template>
	<div class="usermanagement">
		<div class="breadcrumb">
		    <el-breadcrumb separator="/" >
		        <el-breadcrumb-item to="/home"></el-breadcrumb-item>
		        <el-breadcrumb-item></el-breadcrumb-item>
		    </el-breadcrumb>
		</div>
		<div class="article">
			<h2></h2>
			<div class="edit">
				<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
					<el-form-item label="">
						<el-input placeholder=""  v-model="searchItem.likeName"></el-input>
					</el-form-item>
					<el-form-item class="sysTemButton">
						<el-button type="primary" ></el-button>
						<el-button type="info" :plain="true" @click="openIsAdd"></el-button>
					</el-form-item>
				</el-form>
			</div>
			<div class="table">
				<el-table border style="width: 100%" height="550" :data="allItems">
					<el-table-column prop="emoj_path" label="">
						<template scope="scope">
						        <img style="height: 110px;width: 120px;margin-top: 10px;"   :src="scope.row.emoj_path" alt="" />
						</template>
					</el-table-column>
					<el-table-column prop="emoj_assort" label=""></el-table-column>
					<el-table-column label="" width="200">
						<template scope="scope">
							<el-button type="text" @click="openEditDialogMethod(scope.$index)"> &amp; </el-button>
						</template>
					</el-table-column>
					<el-table-column label="" width="200">
						<template scope="scope">
							<el-button type="text" @click="deleteSystem(scope.$index)"></el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
		</div>
			<el-dialog title=" & " v-model="isEdit">
				
					<el-table border style="width: 100%" height="440" :data="tableData">
							<el-table-column prop="emojPath" label="" >
								<template scope="scope">
								        <img style="height: 110px;width: 120px;"   :src="scope.row.emojPath" alt="" />
								</template>
							</el-table-column>
							<el-table-column prop="emojCode" label="">
								<el-input placeholder=""  ></el-input>
							</el-table-column>
							<el-table-column prop="emojName" label=""></el-table-column>
							<el-table-column label="" width="100">
							<template scope="scope">	
							<el-button type="text" @click="editorSys(scope.$index)"></el-button>
							</template>
							</el-table-column>
							<el-table-column label="" width="100">
							<template scope="scope">	
							<el-button type="text" @click="deleteSystemFace(scope.$index)"></el-button>
							</template>
							</el-table-column>
					</el-table>
			</el-dialog>
			<el-dialog title="" v-model="isAdd">
			  <div>
				<el-form ref="form" :model="form" label-width="100px" id="form_data">
				  <el-form-item label="">
				    <el-input v-model="form.name" style="width: 200px;" name="emojAssort" @blur="filter" onsubmit="return false;" placeholder=""></el-input>
				  </el-form-item>
				  <el-form-item label="">
				    <input type="file"   name="multipartFiles" multiple="multiple" accept="image/x-png,image/gif,image/jpeg,image/bmp"/>
				  </el-form-item>
				  <el-form-item>
				    <el-button type="primary" @click="onSubmit"></el-button>
				    <el-button @click="closeIsAdd"></el-button>
				  </el-form-item>
				</el-form>
			</div>
			</el-dialog>
			
			<el-dialog title="" v-model="isEditor">
				<el-form :model="sysTem">	
					<el-form-item label="">
						<el-input placeholder=""  v-model="sysTem.emojCode"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input placeholder=""  v-model="sysTem.emojName"></el-input>
					</el-form-item>
				<el-form-item >
						<el-button type="primary" @click="change"></el-button>
				</el-form-item>
				</el-form>
			
			</el-dialog>
	</div>
</template>
<script>
	import axios from 'axios'
	import qs from 'qs'

	export default {
		data () {
			return {
				isEditor:false,
				isEdit: false,
				isAdd:false,
				sysIndex:'',
				tableData:[{
					emojPath:'',
					emojCode:'',
					emojAssort:''
				}],
				sysTem:{},
				//  v-model 
				searchItem: {
					likeID: '',
				},
				// 
				allItems: [{
					emoj_path: '',
					emoj_assort: '',
					emojCode: '',
				}],
				formLabelWidth: '120px',
				form: {}
			};
		},
		
		       
		created () {
			//  allItems
			this.getAllItemsMethod();
		},
	
		methods: {
			//
		getAllItemsMethod(){
			axios.post('eyed/web/emoji/loadEmojPackage.action')
			.then((response)=>{
				this.allItems = response.data.result;
				console.log(response.data.result)
			})
			.catch((error)=>{
				console.log(error);
			})
		},
		//
		openEditDialogMethod(i){
			this.sysIndex=i;
			this.isEdit=true
			axios.post('/eyed/web/emoji/selectByAssort.action',qs.stringify({
				assort:this.allItems[i].emoj_assort
			}))
			.then((response)=>{
				this.tableData = response.data.result;
				console.log(response.data.result)
			})
			.catch((error)=>{
				console.log(error);
			})
			  this.$nextTick(function(){
			  	 var dligo=document.getElementsByClassName("el-table__body-wrapper")[1].scrollTop=0;
			  	 console.log(dligo)
			  })
		},
		//
		deleteSystem(i){
				//
          this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          }).then(() => {
          	//
        	axios.post('/eyed/web/emoji/deleteAssort.action',qs.stringify({
				assort:this.allItems[i].emoj_assort
				
			}))
			.then((response)=>{
				this.getAllItemsMethod();
			})
			.catch((error)=>{
				console.log(error);
			})

        }).catch(() => {

        });
			
		},
		//
		deleteSystemFace(i){
						//
          this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          closeOnClickModal:true
          }).then(() => {
          	//
        	console.log(this.tableData[i].emojId)
			axios.post('/eyed/web/emoji/delete.action',qs.stringify({
			emojId:this.tableData[i].emojId
			}))
			.then((response)=>{
				this.openEditDialogMethod(this.sysIndex)
			})
			.catch((error)=>{
				console.log(error);
			})

        }).catch(() => {

        });
			
			
			
			
			
			
		},
	
		openIsAdd(){
			this.isAdd=true
		},
		closeIsAdd(){
			this.isAdd=false
		},
		
		editorSys(i){
			this.isEditor=true
			console.log(i)
			this.sysTem=this.tableData[i]
		},
		change(){
			axios.post('/eyed/web/emoji/modify.action',qs.stringify({
			emojId:this.sysTem.emojId,
			emojName:this.sysTem.emojName,
			emojCode:this.sysTem.emojCode
			}))
			.then((response)=>{
				this.openEditDialogMethod(this.sysIndex)
				alert("")
				this.isEditor=false
			})
			.catch((error)=>{
				console.log(error);
			})
		},
		//
		onSubmit() {
          	 var data = new FormData(document.getElementById("form_data")); 
	     	 axios({
	     	 	method:'post',
	     	 	url:'/eyed/web/emoji/addDemojBatch.action',
	     	 	data:data,
	     	 	contentType: false,
	     	 	cache: false,
	     	 }).then((response)=>{
	     	 		if (response.data.code == '0000') {
	     	 		this.getAllItemsMethod();
						console.log(response.data.result)
						
					} else {
						console.log('0000');
					};
	     	 })
	     	 	.catch((error) => {
					console.log(error);
				});


      },
      //
      filter(){
//  		this.form.name =this.filterStr(this.form.name)
    		this.checkval(this.form.name)
      },
      //
      filterStr(str){    
        var pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}'%+_]");    
        var specialStr = "";    
        for(var i=0;i<str.length;i++)    
        {    
             specialStr += str.substr(i, 1).replace(pattern, '');     
        }    
        return specialStr;    
    },
     checkval(t) {
    var re = /^[\u4e00-\u9fa5a-z]+$/gi;//
    if(t){
    	if (re.test(t)) {
        return true;
    } else {
    	alert("")
        return false;
    }
    }
    
		},
	},	
		
	};
</script>
<style>
	.sysTemButton{
		position: absolute;
		top: 195px;
		left: 570px;
	}
	.sysQuery{
		margin-left: 20px;
		position: absolute;
		top: 71px;
		right: 100px;
	}
</style>


