<template>
	<div class="usermanagement">
		<div class="breadcrumb">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>

		<div class="article">
			<h2></h2>

			<div class="edit">
				<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
					<el-form-item label="" >
						<el-input placeholder=""  v-model="searchItem.labelid" @keyup.enter.native="searchItemMethod"></el-input>
					</el-form-item>
					<el-form-item >
						<el-button type="primary"  @click="searchItemMethod" ></el-button>
						<el-button type="info" :plain="true" @click="openAddDialogMethod" ></el-button>
					</el-form-item>
				</el-form>
			</div>

			<div class="table">
				<el-table border style="width: 100%" height="440" :data="allItems">
					<el-table-column prop="labelid" label="ID"></el-table-column>
					<el-table-column prop="labelname" label=""></el-table-column>
					<el-table-column prop="labelverb" label=""></el-table-column>
					<el-table-column prop="labelimagepath" label="">
						 <template scope="scope">
						        <img style="height: 110px;width: 120px;margin-top: 10px;"   :src="scope.row.labelimagepath" alt="" />
						 </template>
					</el-table-column>
					<el-table-column prop="parentlabelid" label="id"></el-table-column>
					<el-table-column prop="personality" label=""></el-table-column>
					<el-table-column label="" width="300">
						<template scope="scope">
							<el-button type="text" @click="openEditDialogMethod(scope.$index)"> &amp; </el-button>
						<!--	<el-button type="text"  @click="deleteItemMethod(scope.$index)"></el-button>-->
						</template>
					</el-table-column>
				</el-table>
			</div>

			<el-dialog title="" v-model="isAdd">
				<el-form :model="addItem" enctype="multipart/form-data" id="labelIn">
					<el-form-item label=" ID" :label-width="formLabelWidth" >
						<el-input auto-complete="off" :disabled="true" v-model="addItem.labelid" class="lablewidth"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="addItem.labelname" class="lablewidth" name="labelname"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="addItem.labelverb" class="lablewidth" name="labelverb"></el-input>
					</el-form-item>
					
			<el-form-item label="" :label-width="formLabelWidth" >
		      <el-input auto-complete="off" type="file" name="img"  ></el-input>
		  
		    </el-form-item>
					<el-form-item label="id" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="long" v-model="addItem.parentlabelid" class="lablewidth" name="parentlabelid"></el-input>
					</el-form-item>
					<el-form-item label=" " :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="long" v-model="addItem.personality" class="lablewidth" name="personality"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="addItemMethod"> </el-button>
					<el-button @click="closeAddDialogMethod"> </el-button>
				</div>
			</el-dialog>

			<el-dialog title=" & " v-model="isEdit">
				<el-form :model="addItem" enctype="multipart/form-data" id="labelChange">
					<el-form-item label=" ID" :label-width="formLabelWidth" >
						<el-input auto-complete="off" :readonly="true" v-model="addItem.labelid" class="lablewidth" name="labelid"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="addItem.labelname" class="lablewidth" name="labelname"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="addItem.labelverb" class="lablewidth" name="labelverb"></el-input>
					</el-form-item>
					
			<el-form-item label="" :label-width="formLabelWidth" >
		      <el-input auto-complete="off" type="file" name="img"  class="lablewidth"></el-input>
		  
		    </el-form-item>
					<el-form-item label="id" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="long" v-model="addItem.parentlabelid" class="lablewidth" name="parentlabelid"></el-input>
					</el-form-item>
					<el-form-item label=" " :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="long" v-model="addItem.personality" class="lablewidth" name="personality"></el-input>
					</el-form-item>
				</el-form>
				
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="editItemMethod"></el-button>
					<el-button @click="closeEditDialogMethod"> </el-button>
				</div>
			</el-dialog>

		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'

	export default {
		data () {
			return {
		
				//  v-model 
				searchItem: {
					likeName: '',
				},

				// 
				allItems: [{
					labelid: '',
					//createtime: '',
					labelname: '',
					labelverb: '',
					labelimagepath: '',
					parentlabelid: '',
					personality: '',
				}],

				formLabelWidth: '120px',
				// add  flag v-model 
				isAdd: false,
				addItem: {
					labelid: '',
					//createtime: '',
					labelname: '',
					labelverb: '',
					labelimagepath: '',
					parentlabelid: '',
					personality: '',
				},

				// edit  flag editIndex v-model 
				isEdit: false,
				editIndex: '',
				editItem: {},
				//saveItem: {},
			};
		},

		created () {
			//  allItems
			this.getAllItemsMethod();
		},

		methods: {
			//  allItems
			getAllItemsMethod () {
				axios.get('/eyed/web/label/selectAllLabel.action')
				.then((response) => {
					this.allItems = response.data.result;
					console.log(response.data.result)
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			openAddDialogMethod () {
				this.isAdd = true;
			},
			closeAddDialogMethod () {
				this.isAdd = false;
				// 
				this.addItem = {
					labelid: '',
					//createtime: '',
					labelname: '',
					labelverb: '',
					labelimagepath: '',
					parentlabelid: '',
					personality: '',
				};
			},
			addItemMethod () {
			 var data = new FormData(document.getElementById("labelIn")); 
	     	 axios({
	     	 	method:'post',
	     	 	url:'/eyed/web/label/addInterest.action',
	     	 	data:data,
	     	 	contentType: false,
	     	 	cache: false,
	     	 }).then((response)=>{
	     	 		if (response.data.code == '0000') {
	     	 			this.getAllItemsMethod()
						console.log(response.data.result)
						
					} else {
						console.log('0000');
					};
	     	 })
	     	 	.catch((error) => {
					console.log(error);
				});
			},

			// 
			deleteItemMethod (index) {
				let deleteItem = this.allItems[index];
				axios.post('/eyed/web/label/deleteLabel.action', qs.stringify({
					labelid: deleteItem.labelid,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						//  index 
						this.allItems.splice(index, 1);
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			//  & 
			clone (obj) {
				let newObj = {};
				for (let key in obj) {
					let val = obj[key];
					newObj[key] = typeof val === 'object' ? this.clone(val) : val;
				}
				return newObj;
			},
			openEditDialogMethod (index) {
				this.isEdit = true;
				//  item
				this.addItem= index;
				this.addItem = this.clone(this.allItems[index]);
			},
			closeEditDialogMethod () {
				this.isEdit = false;
				// 
				this.addItem = {
					labelid: '',
					//createtime: '',
					labelname: '',
					labelverb: '',
					labelimagepath: '',
					parentlabelid: '',
					personality: '',
				};
			},
			editItemMethod () {
			 var data = new FormData(document.getElementById("labelChange")); 
	     	 axios({
	     	 	method:'post',
	     	 	url:'/eyed/web/label/updateInterest.action',
	     	 	data:data,
	     	 	contentType: false,
	     	 	cache: false,
	     	 }).then((response)=>{
	     	 		if (response.data.code == '0000') {
	     	 			this.getAllItemsMethod()
						console.log(response.data.result)
						
					} else {
						console.log('0000');
					};
	     	 })
	     	 	.catch((error) => {
					console.log(error);
				});
				this.isEdit = false;
				// 
				this.addItem = {
					labelid: '',
					//createtime: '',
					labelname: '',
					labelverb: '',
					labelimagepath: '',
					parentlabelid: '',
					personality: '',
				};
			},

			// 
			searchItemMethod () {
				let searchItem = this.searchItem;
				axios.post('/eyed/web/label/selectLabelLikeName.action', qs.stringify({
					likeName:searchItem.labelid,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.allItems = response.data.result;
						console.log(searchItem.labelid);
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},
		},
	};
</script>
<style>
	.lablewidth input{
		width: 48%!important;
	}
</style>


