<template>
	<div class="usermanagement">
		<div class="breadcrumb">
		    <el-breadcrumb separator="/" >
		        <el-breadcrumb-item to="/home"></el-breadcrumb-item>
		        <el-breadcrumb-item></el-breadcrumb-item>
		    </el-breadcrumb>
		</div>

		<div class="article">
			<h2></h2>

			<div class="edit">
				<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
					<el-form-item label="ID">
						<el-input placeholder="ID"  v-model="searchItem.likeName" @keyup.enter.native="searchItemMethod"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary"  @click="searchItemMethod"></el-button>
						<el-button type="info" :plain="true" @click="openAddDialogMethod"></el-button>
					</el-form-item>
				</el-form>
			</div>

			<div class="table">
				<el-table border style="width: 100%" height="440" :data="allItems">
					<el-table-column prop="emojId" label="ID"></el-table-column>
					<el-table-column prop="emojAssort" label=""></el-table-column>
					<el-table-column prop="emojPath" label=""></el-table-column>
					<el-table-column prop="emojName" label=""></el-table-column>
					<el-table-column prop="emojCode" label=""></el-table-column>
					<el-table-column label="" width="300">
						<template scope="scope">
							<el-button type="text" @click="openEditDialogMethod(scope.$index)"> &amp; </el-button>
							<!--<el-button type="text"  @click="deleteItemMethod(scope.$index)"></el-button>-->
						</template>
					</el-table-column>
				</el-table>
			</div>

			<el-dialog title="" v-model="isAdd">
				<el-form :model="addItem">
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" v-model="addItem.emojId"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="addItem.emojAssort"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="addItem.emojPath"></el-input>
					</el-form-item>
					
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="addItem.emojName"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="addItem.emojCode"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="addItemMethod"> </el-button>
					<el-button @click="closeAddDialogMethod"> </el-button>
				</div>
			</el-dialog>

			<el-dialog title=" & " v-model="isEdit">
				<el-form :model="editItem">
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" v-model="editItem.emojId"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.emojAssort"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.emojPath"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.emojName"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.emojCode"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="editItemMethod"></el-button>
					<el-button @click="closeEditDialogMethod"> </el-button>
				</div>
			</el-dialog>

		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'

	export default {
		data () {
			return {


				//  v-model 
				searchItem: {
					likeName: '',
				},

				// 
				allItems: [{
					emojId: '',
					emojAssort: '',
					emojPath: '',
					emojName: '',
					emojCode: '',
				}],

				formLabelWidth: '120px',
				// add  flag v-model 
				isAdd: false,
				addItem: {
					emojId: '',
					emojAssort: '',
					emojPath: '',
					emojName: '',
					emojCode: '',
				},

				// edit  flag editIndex v-model 
				isEdit: false,
				editIndex: '',
				editItem: {},
				//saveItem: {},
			};
		},

		created () {
			//  allItems
			this.getAllItemsMethod();
		},

		methods: {
			//  allItems
			getAllItemsMethod () {
				axios.get('/eyed/web/selectAllDEMOJ.action')
				.then((response) => {
					this.allItems = response.data.result;
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			openAddDialogMethod () {
				this.isAdd = true;
			},
			closeAddDialogMethod () {
				this.isAdd = false;
				// 
				this.addItem = {
					emojId: '',
					emojAssort: '',
					emojPath: '',
					emojName: '',
					emojCode: '',
				};
			},
			addItemMethod () {
				let addItem = this.addItem;
				axios.post('/eyed/web/addDEMOJ.action', qs.stringify({
					emojId: addItem.emojId,
					emojAssort: addItem.emojAssort,
					emojPath: addItem.emojPath,
					emojName: addItem.emojName,
					emojCode: addItem.emojCode,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						//  allItems
						this.getAllItemsMethod();
						//  dialog
						this.closeAddDialogMethod();
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			deleteItemMethod (index) {
				let deleteItem = this.allItems[index];
				axios.post( '/eyed/web/deleteDEMOJ.action', qs.stringify({
					demojid: deleteItem.emojId,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						//  index 
						this.allItems.splice(index, 1);
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			//  & 
			clone (obj) {
				let newObj = {};
				for (let key in obj) {
					let val = obj[key];
					newObj[key] = typeof val === 'object' ? this.clone(val) : val;
				}
				return newObj;
			},
			openEditDialogMethod (index) {
				this.isEdit = true;
				//  item
				this.editIndex = index;
				this.editItem = this.clone(this.allItems[index]);
			},
			closeEditDialogMethod () {
				this.isEdit = false;
				// 
				this.editItem = {
					emojId: '',
					emojAssort: '',
					emojPath: '',
					emojName: '',
					emojCode: '',
				};
			},
			editItemMethod () {
				let editItem = this.editItem;
				axios.post('/eyed/web/updateDEMOJ.action', qs.stringify({
					emojId: editItem.emojId,
					emojAssort: editItem.emojAssort,
					emojPath: editItem.emojPath,
					emojName: editItem.emojName,
					emojCode: editItem.emojCode,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.closeEditDialogMethod();
						this.getAllItemsMethod();
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			searchItemMethod () {
				let searchItem = this.searchItem;
				axios.post('/eyed/web/seletcdDEMOJLikeName.action', qs.stringify({
					likeName:searchItem.likeName,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.allItems= response.data.result;
						console.log(searchItem.likeName)
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},
		},
	};
</script>



