<template>
	<div class="blockdL">
		<div class="breadcrumb">
		    <el-breadcrumb separator="/">
		        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
		        <el-breadcrumb-item></el-breadcrumb-item>
		    </el-breadcrumb>
		</div>

		<div class="article">
			<h2></h2>

			<div class="edit">
				<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
					<el-form-item label="">
						<el-input placeholder=""  v-model="searchItem.postlabelname" @keyup.enter.native="searchItemMethod"></el-input>
					</el-form-item>

					<el-form-item>
						<el-button type="primary"  @click="searchItemMethod"></el-button>
						<el-button type="info" :plain="true" @click="openAddDialogMethod"></el-button>
					</el-form-item>
				</el-form>
			</div>

			<div class="table">
				<el-table border style="width: 100%" height="440" :data="allItems">
					<el-table-column prop="postlabelid" label=""></el-table-column>
					<el-table-column prop="postlabelname" label=""></el-table-column>
					<el-table-column prop="sort" label=""></el-table-column>
					<el-table-column label="" width="300">
						<template scope="scope">
							<el-button type="text" @click="openEditDialogMethod(scope.$index)"> &amp; </el-button>
							<!--<el-button type="text"  @click="deleteItemMethod(scope.$index)"></el-button>-->
						</template>
					</el-table-column>
				</el-table>
			</div>

			<el-dialog title="" v-model="isAdd">
				<el-form :model="addItem">
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" 	 v-model="addItem.postlabelname"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="long" v-model="addItem.sort"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="addItemMethod"> </el-button>
					<el-button @click="closeAddDialogMethod"> </el-button>
				</div>
			</el-dialog>

			<el-dialog title=" & " v-model="isEdit">
				<el-form :model="editItem">
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" v-model="editItem.postlabelid"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="" v-model="editItem.postlabelname"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="" v-model="editItem.sort"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="editItemMethod"></el-button>
					<el-button @click="closeEditDialogMethod"> </el-button>
				</div>
			</el-dialog>
	</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'

	export default {
		data () {
			return {
				//  v-model 
				searchItem: {
					postlabelname: '',
				},

				// 
				allItems: [{
					postlabelid: '',
					postlabelname: '',
					sort: '',
				}],

				formLabelWidth: '120px',
				// add  flag v-model 
				isAdd: false,
				addItem: {
					postlabelname: '',
					sort: '',

				},

				// edit  flag editIndex v-model 
				isEdit: false,
				editIndex: '',
				editItem: {
//					postlabelname: '',
//					sort: '',
				},
				//saveItem: {},
			};
		},

		created () {
			//  allItems
			this.getAllItemsMethod();
		},

		methods: {
			//  allItems
			getAllItemsMethod () {
				axios.get('/eyed/web/postLabel/selectPostLabel.action')
				.then((response) => {
					this.allItems = response.data.result;
					console.log(response.data.result);
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			openAddDialogMethod () {
				this.isAdd = true;
			},
			closeAddDialogMethod () {
				this.isAdd = false;
				// 
				this.addItem = {
					postlabelname: '',
					sort: '',
				};
			},
			//
			addItemMethod () {
				let addItem = this.addItem;
				axios.post('/eyed/web/postLabel/addPostLabel.action', qs.stringify({
					postlabelname: addItem.postlabelname,
					sort: addItem.sort,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						//  allItems
						this.getAllItemsMethod();
						//  dialog
						this.closeAddDialogMethod();
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			deleteItemMethod (index) {
				let deleteItem = this.allItems[index];
				axios.post('/eyed/web/postLabel/deletePostLabel.action', qs.stringify({
					postlabelid: deleteItem.postlabelid,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						//  index 
						this.allItems.splice(index, 1);
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			//  & 
			clone (obj) {
				let newObj = {};

				for (let key in obj) {
					let val = obj[key];
					newObj[key] = typeof val === 'object' ? this.clone(val) : val;
				}
				return newObj;

			},
			openEditDialogMethod (index) {
				this.isEdit = true;
				//  item
				this.editIndex = index;
				this.editItem = this.clone(this.allItems[index]);
			},
			closeEditDialogMethod () {
				this.isEdit = false;
				// 
				this.editItem = {
					postlabelid:'',
					postlabelname: '',
					sort: '',
				};
			},
			//
			editItemMethod () {
				let editItem = this.editItem;
				axios.post('/eyed/web/postLabel/updatePostLabel.action', qs.stringify({
					postlabelname:editItem.postlabelname,
					sort: editItem.sort,
					postlabelid:editItem.postlabelid,

				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.closeEditDialogMethod();
						this.getAllItemsMethod();

					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			searchItemMethod () {
				let searchItem = this.searchItem;
				axios.post('/eyed/web/postLabel/selectPostLabel.action', qs.stringify({
					postlabelid:searchItem.postlabelname,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.allItems= response.data.result;
						console.log(this.allItems)
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},
		},
	};
</script>



