<template>
	<div class="userActivity">
		<div class="breadcrumb">
		    <el-breadcrumb separator="/">
		        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
		        <el-breadcrumb-item></el-breadcrumb-item>
		        <el-breadcrumb-item></el-breadcrumb-item>
		    </el-breadcrumb>
		</div>

		<div class="article">
					<div class="edit disa" >
						<el-form label-width="120px" :model="searchItemQ" onsubmit="return false;">
							<el-form-item label="" class='disa'>
								<el-input placeholder="ID"  v-model="searchItemQ.groupID" @keyup.enter.native="searchItemMethodQ"></el-input>
							</el-form-item >
								<el-button type="primary"  @click="searchItemMethodQ" id="search"></el-button>
						</el-form>

					</div>
			<div class="edit disa" >
						<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
							<el-form-item label="" class='disa'>
								<el-input placeholder=""  v-model="searchItem.eyedID" @keyup.enter.native="searchItemMethod"></el-input>
							</el-form-item>
								<el-button type="primary"  @click="searchItemMethod" id="searchtow"></el-button>
						</el-form>
					</div>

						<el-form label-width="80px" :model="center" class='disa' >
								<el-form-item label=":" class='disa' id="longitude">
									<el-input placeholder="" style="width: 100px;"  v-model.number="center.lng"  ></el-input>
								</el-form-item>
								<el-form-item label=":" class='disa'>
									<el-input placeholder=""  style="width: 100px;" v-model.number="center.lat" ></el-input>
								</el-form-item>
								<el-button type="primary" id="chaxun"></el-button>
						</el-form>
			<h2></h2>

			<div class="table">
				<el-table border  height="600" :data="allItems" class="tab">
					<el-table-column prop="groupID" label="ID" width="90"></el-table-column>
					<el-table-column prop="groupName" label=""></el-table-column>
					<el-table-column prop="eyedID" label=""></el-table-column>
					<el-table-column prop="nickName" label=""></el-table-column>
					<el-table-column prop="groupLongitude" label=""></el-table-column>
					<el-table-column prop="groupLatitude" label=""></el-table-column>
					<el-table-column label="">
						<template scope="scope">
							<el-button type="text" @click="openEditDialogMethod(scope.$index)"> </el-button>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template scope="scope">
							<el-button type="text" @click="openEditDialogMethod2(scope.$index)"> </el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
				<div class="block" style="position: absolute;bottom: 40px;">
			    <el-pagination
			      :current-page="page"
			      layout="prev, pager, next, jumper"
			      :total="total"
			      @current-change="activityManagement"
			      >
			    </el-pagination>
			  </div>
			
			
			
					  <div id="map"></div>
			<el-dialog title=" & " v-model="isEdit">
					<el-table border  height="600" :data="allItems">
						<el-table-column prop="groupID" label="ID" width="90"></el-table-column>
						<el-table-column prop="groupName" label=""  width="130"></el-table-column>
						<el-table-column prop="eyedID" label="ID" width="90"></el-table-column>
						<el-table-column prop="nickName" label=""  width="120"></el-table-column>
						<el-table-column prop="groupLocation" label="" width="180"></el-table-column>
						<el-table-column prop="userID" label="ID"></el-table-column>
						<el-table-column prop="createTime" label="" width="120">
						<template scope="scope">
					        <span >{{ scope.row.createTime|discount}}</span>
					    </template>
						</el-table-column>
						<el-table-column prop="startTime" label="" width="120">
						<template scope="scope">
					        <span >{{ scope.row.startTime|discount}}</span>
					    </template>
						</el-table-column>
						<el-table-column prop="endTime" label="" width="120">
						<template scope="scope">
					        <span >{{ scope.row.endTime|discount}}</span>
					    </template>
						</el-table-column>
						<el-table-column prop="groupLongitude" label=""></el-table-column>
						<el-table-column prop="groupLatitude" label=""></el-table-column>
					</el-table>

			</el-dialog>
	</div>
	</div>
</template>
<script>

	import axios from 'axios'
	import qs from 'qs'

	export default {
		data () {
			return {
				//
				page:1,
				//
				total:1,
				//  v-model 
				searchItem: {
					eyedID: '',

				},
				aa:'',
				searchItemQ:{
					groupID: '',
				},
				// 
				allItems: [{
					groupID: '',
					groupName: '',
					eyedID: '',
					nickName: '',
					groupLocation: '',
					userID: '',
					createTime: '',
					startTime: '',
					endTime: '',
					groupLongitude: '',
					groupLatitude: '',

				}],

				formLabelWidth: '120px',
				// add  flag v-model 

				// edit  flag editIndex v-model 
				isEdit: false,
				editIndex: '',
				editItem: {},
				//saveItem: {},
				 center: {
			        lng: '',
			        lat:''
			      },
			      zoom: 15,

			};
		},

		created () {
			//  allItems
			this.getAllItemsMethod();
		},
		//JS
		mounted:function(){
		var map = new BMap.Map("map");
		map.enableScrollWheelZoom(true);
		map.centerAndZoom(new BMap.Point(116.401398,39.914445),15);
		var chaxun = document.getElementById("chaxun")
		chaxun.onclick=function(){
			if(document.getElementsByClassName("el-input__inner")[2].value != "" && document.getElementsByClassName("el-input__inner")[3].value != ""){
				map.clearOverlays();
				var new_point = new BMap.Point(document.getElementsByClassName("el-input__inner")[2].value,document.getElementsByClassName("el-input__inner")[3].value);
				var marker = new BMap.Marker(new_point);  // 
				map.addOverlay(marker);              // 
				marker.enableDragging();  			//
				marker.setAnimation(BMAP_ANIMATION_BOUNCE); //
				map.panTo(new_point);
				}
			marker.addEventListener("dragend", function(e){
   				document.getElementsByClassName("el-input__inner")[2].value=e.point.lng;
   				document.getElementsByClassName("el-input__inner")[3].value=e.point.lat;
		})
		}
		},
		methods: {
			fireFox(){
				 if (navigator.userAgent.indexOf('Firefox') >= 0){
				 	alert('Firefox')
				 	var inputTop = document.getElementById("search")
				 	inputTop.setAttribute("style","position: relative;top: -12px;")
				 	console.log(inputTop)
				 	}
			},

			 	syncCenter (e) {
			      const {lng, lat} = e.target.getCenter();
			      this.center.lng = lng;
			      this.center.lat = lat;
			    },
			    syncZoom (e) {
			      this.zoom = e.target.getZoom();
			    },

			//  allItems
			getAllItemsMethod () {
				axios.post('/eyed/web/eyedGroup/selectALLGroupImformations.action', qs.stringify({
					current:1,
				}))
				
				.then((response) => {
					this.allItems = response.data.result.resultList;
					this.total = response.data.result.count/5
					console.log(response.data.result)
				})
				.catch((error) => {
					console.log(error);
				});
			},
			//
			activityManagement(val){
				let searchItemQ = this.searchItemQ;
				let searchItem = this.searchItem;
				//
				document.getElementsByClassName("el-table__body-wrapper")[0].scrollTop=0
				this.page=val;
				axios.post('/eyed/web/eyedGroup/selectALLGroupImformations.action', qs.stringify({
					current:val,
					groupID:searchItemQ.groupID,
					eyedID:searchItem.eyedID,
				}))
				.then((response) => {
					this.allItems = response.data.result.resultList;
					
				})
				.catch((error) => {
					console.log(error);
				});
			},
			//  & 
			clone (obj) {
				let newObj = {};
				for (let key in obj) {
					let val = obj[key];
					newObj[key] = typeof val === 'object' ? this.clone(val) : val;
				}
				return newObj;
			},
			
			openEditDialogMethod (index) {
				this.isEdit = true;
				//  item

				this.editIndex = index;
				this.editItem = this.clone(this.allItems[index]);
			},
			
			openEditDialogMethod2(index){
				this.aa=true;
				this.editIndex = index;
				document.getElementsByClassName("el-input__inner")[2].value=parseFloat(this.allItems[index].groupLongitude);
   				document.getElementsByClassName("el-input__inner")[3].value=parseFloat(this.allItems[index].groupLatitude);
   				//openEditDialogMethod2chaxun
				chaxun.click()
			},
		
			//
			searchItemMethodQ() {
			
        	let searchItemQ = this.searchItemQ;
			//
			this.page=1;
	        axios.post('/eyed/web/eyedGroup/selectALLGroupImformations.action', qs.stringify({
	          groupID:searchItemQ.groupID,
	          
	          current:this.page,
	        }))
	          .then((response) => {
	            if (response.data.code == '0000') {
	              this.allItems= response.data.result.resultList;
	              this.total =  response.data.result.count/5;
	              console.log(response.data.result)
	            } else {
	              alert(response.data.msg);
	              console.log('0000');
	            };
	          })
	          .catch((error) => {
	            console.log(error);
	          });
	      },
			// 
			searchItemMethod () {
				console.log("bbb")
				let searchItem = this.searchItem;
       			this.page=1;
				axios.post('/eyed/web/eyedGroup/selectALLGroupImformations.action', qs.stringify({
					eyedID:searchItem.eyedID,
					current:this.page,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.allItems= response.data.result.resultList;
						this.total =  response.data.result.count/5;
						console.log(response.data.result)
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},
		},
	};
</script>
<style>
	.tab{
		width: 50%;margin:1% 0 0 0;float: left;
	};
	#map{

		width: 45%;
		display: inline-block;
		height: 600px;
		margin:1% 0 0 1%;
		float: left;

	};


</style>



