<template>
	<div class="blockdL">
		<div class="breadcrumb">
		    <el-breadcrumb separator="/">
		        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
		        <el-breadcrumb-item></el-breadcrumb-item>
		    </el-breadcrumb>
		</div>

		<div class="article">
			<h2></h2>

			<div class="edit">
					<div class="edit">
					<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
					<el-form-item label="">
						<el-input placeholder=""  v-model="searchItem.tradeNo" @keyup.enter.native="searchItemMethod"></el-input>
					</el-form-item>

					<el-form-item>
						<el-button type="primary"  @click="searchItemMethod" style="position: absolute;left: 292px;top: -58px;"></el-button>

					</el-form-item>
				</el-form>
			</div>

			</div>

			<div class="table">

			<el-table :data="allItems" border style="width: 100%" max-height="550">
    <el-table-column fixed	prop="userid"  label="id"  width="220">
    </el-table-column>
    <el-table-column prop="eyedid" label=""  width="150">
    </el-table-column>
   <el-table-column prop="hotInformation.liveness" label=""  width="150">
    </el-table-column>
    <el-table-column prop="userage" label=""  width="100">
    </el-table-column>
     <el-table-column prop="hotInformation.nickname" label=""  width="220">
    </el-table-column>
     <el-table-column prop="hotInformation.phonenumber" label=""  >
    </el-table-column>
      <el-table-column prop="userLocation.deviceid" label="IMEI"  >
    </el-table-column>
  
  
    
  
     
    <el-table-column   fixed="right" label=""  width="200">
      <template scope="scope">
      	  <el-button  type="text"  size="small">
          &amp; 
        </el-button>
        <!--<el-button  type="text"  size="small">
          
        </el-button>-->
      </template>
    </el-table-column>
  </el-table>


			</div>
			<!---->
			  <div class="block">
			    <el-pagination
			      @current-change="payWater"
			      :current-page="page"
			      layout="prev, pager, next, jumper"
			      :total="total"
			      >
			    </el-pagination>
			  </div>
			</div>
			

		</div>

</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	export default {
		data(){
			return {

    			//
				page:1,
				//
				total:1,
				allItems:[{
					tradeNo:'',
					walletID:'',
					payName:'',
					businessName:'',
					thirdTradeNo:'',
					status:'',
					startTime:'',
					endTime:'',
					remark:'',
					createTime:'',
					userID:'',
					invoiceValue:'',
					walletCreatTime:'',
					dayLimit:'',
					phoneNumber:''

				}],
				searchItem: {
					reportid: '',
				},
			}
		},
		created () {
			//  allItems
			this.getAllItemsMethod();
		},
		methods:{
			getAllItemsMethod(){
				axios.post('eyed/web/user/findInformations.action',qs.stringify({
					current:1,
					length:3
				}))
				.then((response)=>{
						if (response.data.code == '0000') {
						this.allItems = response.data.result.resultList
						this.total = (response.data.result.count/3)*10
						console.log(response.data.result.resultList)
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error)=>{
					console.log(error);
				});
			},
			payWater(val){
				//
				document.getElementsByClassName("el-table__body-wrapper")[0].scrollTop=0
				this.page=val;
				let searchItem = this.searchItem;
				axios.post('eyed/web/user/findInformations.action', qs.stringify({
					current:val,
					length:3,
					userid:searchItem.tradeNo,
				}))
				.then((response) => {
					this.allItems = response.data.result.resultList;
					console.log(response.data.result.resultList)
				})
				.catch((error) => {
					console.log(error);
				});
			},
			searchItemMethod () {
				this.page=1;
				document.getElementsByClassName("el-table__body-wrapper")[0].scrollTop=0;
				let searchItem = this.searchItem;
				axios.post('eyed/web/user/findInformations.action', qs.stringify({
					eyednumber:searchItem.tradeNo,
					current:1,
					length:3
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						if(response.data.result.count=='0'){
							alert("")
						}else{
						this.allItems= response.data.result.resultList;
						this.total = (response.data.result.count/3)*10
						}
						
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

		}
		}


</script>



