<template>
	<div class="usermanagement">
		<div class="breadcrumb">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>

		<div class="article">
			<h2></h2>

			<div class="edit">
				<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
					<el-form-item label="" style="display: inline-block;">
						<el-input placeholder=""  v-model="searchItem.labelid" @keyup.enter.native="searchItemMethod"></el-input>
					</el-form-item>
					<el-form-item style="display: inline-block;">
						<el-button type="primary"  @click="searchItemMethod" ></el-button>
						<el-button type="info" :plain="true" @click="openAddDialogMethod" ></el-button>
					</el-form-item>
				</el-form>
			</div>

			<div class="table">
				<el-table border style="width: 100%" height="440" :data="allItems">
					<el-table-column prop="labelid" label="ID"></el-table-column>
					<el-table-column prop="labelname" label=""></el-table-column>
					<el-table-column prop="labelimagepath" label="">
						 <template scope="scope">
						        <img style="height: 110px;width: 120px;margin-top: 10px;"   :src="scope.row.labelimagepath" alt="" />
						 </template>
					</el-table-column>
					<el-table-column prop="labeltype" label=""></el-table-column>
					<el-table-column prop="createtime" label="">
							<template scope="scope">
					        <span >{{ scope.row.createtime|discount}}</span>
					    </template>
					</el-table-column>
					<el-table-column label="" width="300">
						<template scope="scope">
							<el-button type="text" @click="openEditDialogMethod(scope.$index)"> &amp; </el-button>
							<!--<el-button type="text"  @click="deleteItemMethod(scope.$index)"></el-button>-->
						</template>
					</el-table-column>
				</el-table>
			</div>

			<el-dialog title="" v-model="isAdd">
				<el-form :model="addItem" enctype="multipart/form-data" id="labelAb">
					<el-form-item label="labelid" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" v-model="addItem.labelid"></el-input>
					</el-form-item>
					<el-form-item label="labelname" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="addItem.labelname" name="labelname"></el-input>
					</el-form-item>
					<el-form-item label="labelimagepath" :label-width="formLabelWidth">
						<el-input auto-complete="off" type="file"  v-model="addItem.img" name="img"></el-input>
					</el-form-item>
					<el-form-item label="labeltype" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="long" v-model="addItem.labeltype" name="labeltype"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="addItemMethod"> </el-button>
					<el-button @click="closeAddDialogMethod"> </el-button>
				</div>
			</el-dialog>

			<el-dialog title=" & " v-model="isEdit" >
				<el-form :model="editItem" enctype="multipart/form-data" id="labelAbc">
					<el-form-item label="id" :label-width="formLabelWidth">
						<el-input auto-complete="off" :readonly="true" v-model="editItem.labelid" name="labelid"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.labelname" name="labelname"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" type="file" name="img" ></el-input>
					</el-form-item>
					<el-form-item label="labeltype" :label-width="formLabelWidth">
						<el-input auto-complete="off" v-model="editItem.labeltype" name="labeltype"></el-input>
					</el-form-item>
					<!--<el-form-item label="" :label-width="formLabelWidth" >
							<el-select v-model="editItem.labeltype" >

								<el-option label="" value="" ></el-option>
							</el-select>
					</el-form-item>-->
					
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="editItemMethod"></el-button>
					<el-button @click="closeEditDialogMethod"> </el-button>
				</div>
			</el-dialog>

		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'

	export default {
		data () {
			return {
		

				//  v-model 
				searchItem: {
					likeName: '',
				},

				// 
				allItems: [{
					labelid: '',
					createTime: '',
					labelname: '',
					labelimagepath: '',
					labeltype: '',
					labelverb: '',
				}],

				formLabelWidth: '120px',
				// add  flag v-model 
				isAdd: false,
				addItem: {
					labelid: '',
					//createtime: '',
					labelname: '',
					labelimagepath: '',
					labeltype: '',
				},

				// edit  flag editIndex v-model 
				isEdit: false,
				editIndex: '',
				editItem: {},
				//saveItem: {},
			};
		},

		created () {
			//  allItems
			this.getAllItemsMethod();
		},

		methods: {
			//  allItems
			getAllItemsMethod () {
				axios.get('/eyed/web/label/selectAllAbility.action')
				.then((response) => {
					this.allItems = response.data.result;
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			openAddDialogMethod () {
				this.isAdd = true;
			},
			closeAddDialogMethod () {
				this.isAdd = false;
				// 
				this.addItem = {
					labelid: '',
					//createtime: '',
					labelname: '',
					labelimagepath: '',
					labeltype: '',
				};
			},
			addItemMethod () {
				let addItem = this.addItem;
				var data = new FormData(document.getElementById("labelAb")); 
				axios({
	     	 	method:'post',
	     	 	url:'/eyed/web/label/addAbility.action',
	     	 	data:data,
	     	 	contentType: false,
	     	 	cache: false,
	     	 }).then((response)=>{
	     	 		if (response.data.code == '0000') {
	     	 			this.getAllItemsMethod()
						console.log(response.data.result)
						
					} else {
						console.log('0000');
					};
	     	 })
	     	 	.catch((error) => {
					console.log(error);
				});
			},

			// 
			deleteItemMethod (index) {
				let deleteItem = this.allItems[index];
				axios.post('/eyed/web/tempLabel/deleteTempLabel.action', qs.stringify({
					labelid: deleteItem.labelid,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						//  index 
						this.allItems.splice(index, 1);
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			//  & 
			clone (obj) {
				let newObj = {};
				for (let key in obj) {
					let val = obj[key];
					newObj[key] = typeof val === 'object' ? this.clone(val) : val;
				}
				return newObj;
			},
			openEditDialogMethod (index) {
				this.isEdit = true;
				//  item
				this.editIndex = index;
				this.editItem = this.clone(this.allItems[index]);
			},
			closeEditDialogMethod () {
				this.isEdit = false;
				// 
				this.editItem = {
					labelid: '',
					//createtime: '',
					labelname: '',
					labelimagepath: '',
					labeltype: '',
				};
			},
			editItemMethod () {
				var data = new FormData(document.getElementById("labelAbc")); 
				axios({
	     	 	method:'post',
	     	 	url:'/eyed/web/label/updateAbility.action',
	     	 	data:data,
	     	 	contentType: false,
	     	 	cache: false,
	     	 }).then((response)=>{
	     	 		if (response.data.code == '0000') {
	     	 			this.getAllItemsMethod()
						
					} else {
						console.log('0000');
					};
	     	 })
	     	 	.catch((error) => {
					console.log(error);
				});
				this.isEdit = false;
			},

			// 
			searchItemMethod () {
				
				let searchItem = this.searchItem;
				axios.post('/eyed/web/label/selectLabelAbilityLikeName.action', qs.stringify({
					likeName:searchItem.labelid
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.allItems= response.data.result;
						console.log(searchItem.labelid);
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});

			},
		},
	};
</script>



