<template>
	<div class="nationalArea">
		<div class="breadcrumb">
		    <el-breadcrumb separator="/">
		        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
		        <el-breadcrumb-item></el-breadcrumb-item>
		    </el-breadcrumb>
		</div>

		<div class="article" >
			<h2></h2>
			<div class="edit disa" >
						<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
							<el-form-item label="" >
								<el-input placeholder=""  v-model="searchItem.name"  @keyup.enter.native="searchItemMethod"></el-input>
							</el-form-item>
							<el-form-item >
								<el-button type="primary"  @click="searchItemMethod" ></el-button>
								<el-button type="primary" :plain="true" @click="openAddDialogMethod"></el-button>
							</el-form-item>
						</el-form>
			</div>

			<div class="table">
				<el-table border style="width: 100%" height="440" :data="allItems">
					<el-table-column prop="countryId" label="ID"></el-table-column>
					<el-table-column prop="countryName" label=""></el-table-column>
					<el-table-column prop="proviceId" label="ID"></el-table-column>
					<el-table-column prop="proviceName" label=""></el-table-column>
					<el-table-column prop="cityId" label="ID"></el-table-column>
					<el-table-column prop="cityName" label=""></el-table-column>
					<el-table-column prop="countyId" label="()ID"></el-table-column>
					<el-table-column prop="countyName" label="()"></el-table-column>
					<el-table-column label="" width="300">
						<template scope="scope">

							<el-button type="text" @click="openEditDialogMethod(scope.$index)"> &amp; </el-button>
							<el-button type="text"  @click="deleteItemMethod(scope.$index)"></el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
			<!---->
			  <div class="block">
			    <el-pagination
			      @current-change="nationalArea"
			      :current-page="page"
			      layout="prev, pager, next, jumper"
			      :total="total"
			      >
			    </el-pagination>
			  </div>
			<el-dialog title="" v-model="isAdd">
				<el-form :model="addItem">
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off"  placeholder="" v-model="addItem.name"></el-input>
					</el-form-item>
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="ID" v-model="addItem.pid"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="addItemMethod"> </el-button>
					<el-button @click="closeAddDialogMethod"> </el-button>
				</div>
			</el-dialog>

			<el-dialog title=" & " v-model="isEdit">
				<el-form :model="editItem">
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" placeholder="string" v-model="editItem.countryId"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.countryName"></el-input>
					</el-form-item>
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" placeholder="string" v-model="editItem.proviceId"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.proviceName"></el-input>
					</el-form-item>
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" placeholder="string" v-model="editItem.cityId"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.cityName"></el-input>
					</el-form-item>
					<el-form-item label="()ID" :label-width="formLabelWidth">
						<el-input auto-complete="off" :disabled="true" placeholder="string" v-model="editItem.countyId"></el-input>
					</el-form-item>
					<el-form-item label="()" :label-width="formLabelWidth">
						<el-input auto-complete="off" placeholder="string" v-model="editItem.countyName"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="editItemMethod"></el-button>
					<el-button @click="closeEditDialogMethod"> </el-button>
				</div>
			</el-dialog>





	</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
//	import { regionDataPlus } from 'element-china-area-data'
	export default {
		data () {
			return {
				//
				page:1,
				//
				total:1,
				//  v-model 
				searchItem: {
					name:'',
				},

				// 
				allItems: [{
					countryId: '',
					countryName: '',
					proviceId: '',
					proviceName: '',
					cityId:'',
					cityName: '',
					countyId: '',
					countyName: '',
				}],

				formLabelWidth: '120px',
				// add  flag v-model 
				isAdd: false,
				addItem: {
					name: '',
					pid: '',

				},

				// edit  flag editIndex v-model 
				isEdit: false,
				editIndex: '',
				editItem: {
					countryId: '',
					countryName: '',
					proviceId: '',
					proviceName: '',
					cityId:'',
					cityName: '',
					countyId: '',
					countyName: '',
				},
				//saveItem: {},
			};
		},

		created () {
			//  allItems
			this.getAllItemsMethod();
//			this.searchItemMethod();
		},

		methods: {
			//  allItems
			getAllItemsMethod () {
				axios.post('/eyed/web/region/selectAreaRegion.action',qs.stringify({
					current:1
				}))
				.then((response) => {
					this.allItems = response.data.result.resultList;
					this.total = response.data.result.count/5
				})
				.catch((error) => {
					console.log(error);
				});
			},
			//
			nationalArea(val){
				//
				document.getElementsByClassName("el-table__body-wrapper")[0].scrollTop=0
				this.page=val;
				let searchItem = this.searchItem;
				axios.post('/eyed/web/region/selectAreaRegion.action', qs.stringify({
					current:val,
					name:searchItem.name,
				}))
				.then((response) => {
					this.allItems = response.data.result.resultList;
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			openAddDialogMethod () {
				this.isAdd = true;
			},
			closeAddDialogMethod () {
				this.isAdd = false;
				// 
				this.addItem = {
					name: '',
					pid: '',
				};
			},
     
			//
			addItemMethod () {
				this.isAdd = false;
				let addItem = this.addItem;
				axios.post('/eyed/web/region/addRegion.action', qs.stringify({
					name: addItem.name,
					pid: addItem.pid,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						//  allItems
						this.getAllItemsMethod();
						//  dialog
						this.closeAddDialogMethod;

						this.searchItemMethod ();
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {

					console.log(error);
				});
			},

			// 
			deleteItemMethod (index) {

			let deleteItem = this.allItems[index];
			let deleteId = null;
			//
			if( deleteItem.countyId){
			    deleteId=deleteItem.countyId;
		        }else if( deleteItem.cityId){
		          deleteId=deleteItem.cityId;
		        }else if(deleteItem.proviceId){
		          deleteId=deleteItem.proviceId;
		        }else{
		          deleteId=deleteItem.countryId;
		        }

			//
          this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          }).then(() => {
          	//
         
          	axios.post( '/eyed/web/region/deleteRegion.action', qs.stringify({
					id:deleteId ,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
					    console.log(deleteId)
						//  index 
//						alert("");
						this.allItems.splice(index, 1);
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});

        }).catch(() => {

        });


			},

			//  & 
			clone (obj) {
				let newObj = {};
				for (let key in obj) {
					let val = obj[key];
					newObj[key] = typeof val === 'object' ? this.clone(val) : val;
				}
				return newObj;
			},
			openEditDialogMethod (index) {
				this.isEdit = true;
				//  item
				this.editIndex = index;
				this.editItem = this.clone(this.allItems[index]);
			},
			closeEditDialogMethod () {
				this.isEdit = false;
				// 
				this.editItem = {
					countryCode: '',
					countryName: '',
					countryNational: '',
					sort: '',
				};
			},
			//
			editItemMethod () {
				let editItem = this.editItem;
				axios.post('/eyed/web/region/updateAreaRegion.action', qs.stringify({
					countryId:editItem.countryId,
					countryName:editItem.countryName,
					proviceId: editItem.proviceId,
					proviceName:editItem.proviceName,
					cityId:editItem.cityId,
					cityName: editItem.cityName,
					countyId: editItem.countyId,
					countyName:editItem.countyName ,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.closeEditDialogMethod();
//						this.getAllItemsMethod();
						this.searchItemMethod ();
						alert("");
					} else {
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},

			// 
			searchItemMethod () {
				document.getElementsByClassName("el-table__body-wrapper")[0].scrollTop=0
				let searchItem = this.searchItem;
				//
				this.page=1;
				axios.post('/eyed/web/region/selectAreaRegion.action', qs.stringify({
					name:searchItem.name,
					current:1,
				}))
				.then((response) => {
					if (response.data.code == '0000') {
						this.allItems = response.data.result.resultList;
						this.total = response.data.result.count/5
					} else {
						alert(response.data.msg);
						console.log('0000');
					};
				})
				.catch((error) => {
					console.log(error);
				});
			},
		},
	};
</script>



