							</el-form-item>
						</el-form>

					</div>
					<h3></h3>
					<el-table border style="width: 100%" height="440" :data="allItemsW">

						<el-table-column prop="payid" label="" width="65"></el-table-column>
						<el-table-column prop="payname" label=""></el-table-column>
						<el-table-column prop="status" label="" width="65"></el-table-column>
						<el-table-column prop="createtime" label="" width="120">
						  <template scope="scope">
					       <!-- <el-icon name="time"></el-icon>-->
					        <span >{{ scope.row.createtime|discount}}</span>
					     </template>
						</el-table-column>
						<el-table-column prop="createname" label="" ></el-table-column>
						<el-table-column  label="" >
							<template scope="scope">
									<el-button type="primary"  @click="payTypeStatus(scope.$index)"></el-button>
								</template>
						</el-table-column>
						<el-table-column  label="" >
							<template scope="scope">
									<el-button type="warning"  @click="openEditDialogMethod(scope.$index)"></el-button>
								</template>
						</el-table-column>
					</el-table>
				</div>

				<div class="table">
						<div class="edit" >
							<el-form label-width="120px" :model="searchItem" onsubmit="return false;">
								<el-form-item label="" >
									<el-input placeholder=""  v-model="searchItem.eyednumber" ></el-input>
								</el-form-item>
								<el-form-item >
									<el-button type="primary"  ></el-button>
									<el-button type="info" :plain="true" @click="openAddDialogMethod2" ></el-button>
								</el-form-item>
							</el-form>
						</div>
					<h3></h3>
					<el-table border style="width: 100%" height="440" :data="allItemsY">
						<el-table-column prop="businessid" label="" width="65"></el-table-column>
						<el-table-column prop="businessname" label=""></el-table-column>
						<el-table-column prop="status" label="" width="65"></el-table-column>
						<el-table-column prop="createtime" label="" width="120">>
						  <template scope="scope">
					      <!--  <el-icon name="time"></el-icon>-->
					        <span>{{ scope.row.createtime|discount}}</span>
					     </template>
						</el-table-column>
						<el-table-column prop="createname" label="" ></el-table-column>
						<el-table-column  label="" >
							<template scope="scope">
									<el-button type="primary"  @click="payTypeStatus2(scope.$index)"></el-button>
								</template>
						</el-table-column>
						<el-table-column  label="" >
							<template scope="scope">
								<span class="wrapper">
									<el-button type="warning" @click="openEditDialogMethod2(scope.$index)"  ></el-button>
								</span>
								</template>
						</el-table-column>

					</el-table>
